@use '../../Foundation/vars' as va;
@use '../../Foundation/mixin';

.p-name{
  //font-family: va.//$font-Japanese;
  font-size:max(calc(14/va.$sp-design-width*100vw));
  color: va.$text-color;
  line-height: calc( 17/14 );
  //letter-spacing: calc(/1000 * 1em);
  //font-weight: 500;
  margin-bottom:calc(37/va.$sp-design-width*100vw);
  @include mixin.mq(pc){
      font-size:max(calc(14/va.$pc-design-width*100vw),16px);
      //line-height: calc( / );
      //letter-spacing: calc(/1000 * 1em);
      margin-bottom:calc(37/va.$pc-design-width*100vw);
  }
}